/* src/pages/TecoTecoPage/sections/JourneySection.css */

.tecoteco-journey-section {
    background: linear-gradient(135deg, #0d0d0d, #1a1a1a);
    border-radius: 12px;
    padding: 50px 30px 40px;
    margin-bottom: 25px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
    text-align: left;
    position: relative;
    min-height: 50vh;
}

.section-tag-header {
    position: absolute;
    top: 20px;
    left: 30px;
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--color-secondary);
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.tecoteco-journey-section .section-title {
    color: var(--color-text-primary);
    font-size: 2.5rem;
    margin-top: 30px;
    margin-bottom: 25px;
    text-align: left;
    display: block;
}

.tecoteco-journey-section .section-subtitle {
    margin-bottom: 35px;
    text-align: left;
    color: var(--color-text-secondary);
    font-size: 1.2rem;
    line-height: 1.6;
}

/* 성과 통계 카드 */
.journey-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 15px;
    margin: 30px 0 40px 0;
    padding: 20px;
    background: rgba(26, 26, 26, 0.5);
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 15px 10px;
}

.stat-number {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--color-primary);
    margin-bottom: 5px;
    text-shadow: 0 0 8px rgba(195, 232, 141, 0.3);
}

.stat-label {
    font-size: 0.85rem;
    color: var(--color-text-muted);
    font-weight: 500;
}

/* 여정 목록 */
.journey-list {
    list-style: none;
    padding: 0;
    margin-top: 25px;
    text-align: left;
}

.journey-item {
    margin-bottom: 25px;
    padding: 20px;
    background: rgba(255, 255, 255, 0.02);
    border-radius: 8px;
    border-left: 4px solid var(--color-primary);
    transition: all 0.3s ease;
}

.journey-item:hover {
    background: rgba(255, 255, 255, 0.05);
    transform: translateX(5px);
}

.season-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 10px;
}

.season-icon {
    font-size: 1.5rem;
    line-height: 1;
}

.season-header strong {
    color: var(--color-primary);
    font-weight: 600;
    font-size: 1.1rem;
}

.journey-description {
    font-size: 1rem;
    color: var(--color-text-secondary);
    line-height: 1.5;
    display: block;
    margin-bottom: 15px;
}

.achievements {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.achievement-badge {
    font-size: 0.8rem;
    color: var(--color-secondary);
    background: rgba(130, 170, 255, 0.15);
    padding: 4px 8px;
    border-radius: 12px;
    border: 1px solid rgba(130, 170, 255, 0.3);
    font-weight: 500;
}

/* 이미지 섹션 */
.journey-image-wrapper {
    text-align: left;
    margin-top: 40px;
}

.image-header {
    margin-bottom: 20px;
}

.image-header h4 {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 8px;
    font-weight: 600;
}

.image-header p {
    color: var(--color-text-muted);
    font-size: 1rem;
    margin: 0;
}

.journey-image-wrapper img {
    max-width: 100%;
    height: auto;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: block;
}

.journey-image-wrapper img:hover {
    transform: translateY(-5px) scale(1.01);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
}

/* 마무리 메시지 */
.journey-closing {
    margin-top: 40px;
    padding: 25px;
    background: rgba(195, 232, 141, 0.08);
    border: 1px solid rgba(195, 232, 141, 0.2);
    border-radius: 12px;
    text-align: center;
}

.journey-closing p {
    font-size: 1.1rem;
    color: var(--color-text-primary);
    line-height: 1.6;
    margin: 0;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
    .tecoteco-journey-section {
        padding: 40px 20px 30px;
    }

    .section-tag-header {
        top: 15px;
        left: 20px;
        font-size: 1rem;
    }

    .tecoteco-journey-section .section-title {
        font-size: 2.2rem;
        margin-top: 25px;
        margin-bottom: 20px;
    }

    .tecoteco-journey-section .section-subtitle {
        font-size: 1rem;
        margin-bottom: 25px;
    }

    .journey-stats {
        grid-template-columns: repeat(2, 1fr);
        gap: 12px;
        padding: 15px;
        margin: 25px 0 30px 0;
    }

    .stat-card {
        padding: 12px 8px;
    }

    .stat-number {
        font-size: 1.3rem;
    }

    .stat-label {
        font-size: 0.8rem;
    }

    .journey-list {
        margin-top: 20px;
    }

    .journey-item {
        margin-bottom: 20px;
        padding: 15px;
    }

    .season-header strong {
        font-size: 1rem;
    }

    .journey-description {
        font-size: 0.9rem;
        margin-bottom: 12px;
    }

    .achievement-badge {
        font-size: 0.75rem;
        padding: 3px 6px;
    }

    .image-header h4 {
        font-size: 1.2rem;
    }

    .image-header p {
        font-size: 0.9rem;
    }

    .journey-image-wrapper {
        margin-top: 30px;
    }

    .journey-closing {
        margin-top: 30px;
        padding: 20px;
    }

    .journey-closing p {
        font-size: 1rem;
    }
}

@media (max-width: 480px) {
    .tecoteco-journey-section {
        padding: 30px 15px 25px;
    }

    .section-tag-header {
        top: 10px;
        left: 15px;
        font-size: 0.9rem;
    }

    .tecoteco-journey-section .section-title {
        font-size: 1.8rem;
        margin-top: 20px;
        margin-bottom: 15px;
    }

    .tecoteco-journey-section .section-subtitle {
        font-size: 0.9rem;
        margin-bottom: 20px;
    }

    .journey-stats {
        grid-template-columns: 1fr;
        gap: 10px;
        padding: 12px;
        margin: 20px 0 25px 0;
    }

    .stat-card {
        padding: 10px;
    }

    .stat-number {
        font-size: 1.2rem;
    }

    .journey-item {
        margin-bottom: 15px;
        padding: 12px;
    }

    .season-header {
        gap: 8px;
    }

    .season-icon {
        font-size: 1.3rem;
    }

    .season-header strong {
        font-size: 0.95rem;
    }

    .journey-description {
        font-size: 0.85rem;
        margin-bottom: 10px;
    }

    .achievements {
        gap: 6px;
    }

    .achievement-badge {
        font-size: 0.7rem;
        padding: 2px 5px;
    }

    .image-header h4 {
        font-size: 1.1rem;
    }

    .image-header p {
        font-size: 0.85rem;
    }

    .journey-image-wrapper {
        margin-top: 25px;
    }

    .journey-closing {
        margin-top: 25px;
        padding: 15px;
    }

    .journey-closing p {
        font-size: 0.9rem;
    }
}